n/**\n * Remove default margin\n */\n* {\n  margin: 0;\n  padding: 0;\n  font: inherit;\n}\n\n/**\n * Basic styles for links\n */\na {\n  color: a.theme-color('primary');\n  text-decoration: none;\n\n  @include a.on-event {\n    text-decoration: underline;\n  }\n}\n\n/**\n * Make images easier to work with\n */\nimg,\npicture,\nsvg {\n  max-width: 100%;\n  display: block;\n}\n\n/**\n * Basic styles for buttons\n */\nbutton {\n  border: none;\n  background-color: unset;\n  display: inline-block;\n}\n\n/**\n * Basic styles for inputs\n */\ninput {\n  display: block;\n}\n","// -----------------------------------------------------------------------------\n// This file contains all application-wide Sass mixins.\n// -----------------------------------------------------------------------------\n\n/// Load Sass built-in modules\n@use 'sass:list';\n\n/// Load custom modules\n@use 'variables' as v;\n@use 'functions' as f;\n\n/// Event wrapper\n/// @author Harry Roberts\n/// @param {Bool} $self [false] - Whether or not to include current selector\n/// @link https://twitter.com/csswizardry/status/478938530342006784 Original tweet from Harry Roberts\n@mixin on-event($self: false) {\n  @if $self {\n    &,\n    &:hover,\n    &:active,\n    &:focus,\n    &:focus-within {\n      @content;\n    }\n  } @else {\n    &:hover,\n    &:active,\n    &:focus,\n    &:focus-within {\n      @content;\n    }\n  }\n}\n\n/// Make a context based selector a little more friendly\n/// @author Kitty Giraudel\n/// @param {String} $context\n@mixin when-inside($context) {\n  #{$context} & {\n    @content;\n  }\n}\n\n/// Apply text size and line height\n/// @autor Petros Athanasoulis\n/// @param {String} $size\n@mixin text-setting($size) {\n  font-size: f.text-scale($size);\n  line-height: f.line-height($size);\n}\n","/**\n * Basic typography style for copy text\n */\n\n/// Load custom modules\n@use '../abstracts' as a;\n\n/// Import external fonts\n/// Import 'Roboto' font family\n@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');\n/// Import 'Roboto Condensed' font family\n@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');\n\nbody {\n  font-family: a.$font-stack-primary;\n  @include a.text-setting(400);\n  color: a.theme-color('light');\n  background-color: a.theme-color('background');\n}\n","// -----------------------------------------------------------------------------\n// This file contains all application-wide Sass variables.\n// -----------------------------------------------------------------------------\n\n/// Load Sass built-in modules\n@use 'sass:color';\n\n/// Regular font family\n/// @type List\n$font-stack-primary: 'Roboto', 'Open Sans', 'Helvetica Neue Light', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif !default;\n\n/// Accent font family\n/// @type List\n$font-stack-accent: 'Roboto Condensed', 'Open Sans', 'Helvetica Neue Light', 'Helvetica Neue', 'Helvetica', 'Arial',\n  sans-serif !default;\n\n/// Code (monospace) font family\n/// @type List\n$font-stack-code: 'Courier New', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Monaco', monospace !default;\n\n// Text base font size\n$base-font-size: 1rem;\n\n// Text line height\n$base-line-height: 1.25 * $base-font-size;\n\n// Text font sizes\n$text-settings: (\n  900: (\n    font-size: 3rem,\n    line-height: 3,\n  ),\n  800: (\n    font-size: 2.5rem,\n    line-height: 2.5,\n  ),\n  700: (\n    font-size: 2rem,\n    line-height: 2,\n  ),\n  600: (\n    font-size: 1.5rem,\n    line-height: 1.5,\n  ),\n  500: (\n    font-size: 1.25rem,\n    line-height: 1.25,\n  ),\n  400: (\n    font-size: $base-font-size,\n    line-height: $base-line-height * 1,\n  ),\n  300: (\n    font-size: 0.875rem,\n    line-height: 0.875,\n  ),\n  200: (\n    font-size: 0.75rem,\n    line-height: 0.75,\n  ),\n  100: (\n    font-size: 0.625rem,\n    line-height: 0.625,\n  ),\n);\n\n// -----------------------------------------------------------------------------\n\n/// Border radius\n/// @type Length\n$border-radius: 1em;\n\n// -----------------------------------------------------------------------------\n\n/// Container's maximum width\n/// @type Length\n$max-width: 1180px !default;\n\n// -----------------------------------------------------------------------------\n\n/// Breakpoints map\n/// @prop {String} keys - Keys are identifiers mapped to a given length\n/// @prop {Map} values - Values are actual breakpoints expressed in pixels\n$breakpoints: (\n  'small': 320px,\n  'medium': 768px,\n  'large': 1024px,\n) !default;\n\n// -----------------------------------------------------------------------------\n\n/// Relative or absolute URL where all assets are served from\n/// @type String\n/// @example scss - When using a CDN\n///   $base-url: 'https://cdn.example.com/assets/';\n$base-url: '.' !default;\n","// -----------------------------------------------------------------------------\n// This file contains CSS helper classes.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n\n/**\n * Clear inner floats\n */\n.clearfix::after {\n  clear: both;\n  content: '';\n  display: table;\n}\n\n/**\n * Main content containers\n * 1. Make the container full-width with a maximum width\n * 2. Center it in the viewport\n * 3. Leave some space on the edges, especially valuable on small screens\n */\n.container {\n  max-width: a.$max-width; /* 1 */\n  margin-left: auto; /* 2 */\n  margin-right: auto; /* 2 */\n  padding-left: 20px; /* 3 */\n  padding-right: 20px; /* 3 */\n  width: 100%; /* 1 */\n}\n\n/**\n * Hide text while making it readable for screen readers\n * 1. Needed in WebKit-based browsers because of an implementation bug;\n *    See: https://code.google.com/p/chromium/issues/detail?id=457146\n */\n.hide-text {\n  overflow: hidden;\n  padding: 0; /* 1 */\n  text-indent: 101%;\n  white-space: nowrap;\n}\n\n/**\n * Hide element while making it readable for screen readers\n * Shamelessly borrowed from HTML5Boilerplate:\n * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133\n */\n.visually-hidden {\n  border: 0;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  width: 1px;\n}\n","// -----------------------------------------------------------------------------\n// This file contains all styles related to the header of the site/application.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.header {\n  padding: 1rem 1rem 0.25rem;\n\n  .logo {\n    height: 10rem;\n    background-image: url('../assets/images/logo.svg');\n    background-size: contain;\n    background-position: center;\n    background-repeat: no-repeat;\n  }\n}\n","// -----------------------------------------------------------------------------\n// This file contains all styles related to the footer of the site/application.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts/' as a;\n@use '../themes' as t;\n\n.footer {\n  padding: 1rem 1rem 0.25rem;\n  display: flex;\n  justify-content: center;\n}\n","// -----------------------------------------------------------------------------\n// This file contains all styles related to the main of the site/application.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.content {\n  padding-block: 2rem;\n  flex: 1;\n}\n","// -----------------------------------------------------------------------------\n// This file contains all styles related to the main nav of the site/application.\n// -----------------------------------------------------------------------------\n\n/// Load custom module\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.nav-menu {\n  padding: 0.25rem 1rem 1rem;\n  border-bottom: 0.075rem solid a.theme-color('light');\n  font-family: a.$font-stack-accent;\n  @include a.text-setting(500);\n  line-height: 1;\n\n  &__container {\n    display: flex;\n    list-style: none;\n    justify-content: center;\n    gap: 2rem;\n\n    .nav-menu__button {\n      text-transform: uppercase;\n      border-color: a.theme-color('dark');\n      background-color: a.theme-color(dark);\n      color: a.theme-color('primary');\n      padding-inline: 0.625rem;\n\n      &:focus-visible {\n        box-shadow: none;\n        outline: none;\n        border-color: a.theme-color('primary', 'light');\n        background-color: a.theme-color('dark');\n      }\n      &:hover:not(:disabled) {\n        background-color: a.theme-color('dark');\n        border-color: a.theme-color('primary', 'dark');\n      }\n      &:focus {\n        border-color: a.theme-color('background');\n        outline: 0.0675rem solid a.theme-color('primary');\n      }\n      &:disabled {\n        background-color: a.theme-color('primary', 'transparent');\n        color: a.theme-color('dark', 'transparent');\n        cursor: default;\n      }\n      &:active:not(:disabled) {\n        background-color: a.theme-color('primary', 'light');\n        border-color: a.theme-color('primary', 'light');\n        color: a.theme-color('dark');\n        outline: none;\n      }\n    }\n  }\n}\n","// -----------------------------------------------------------------------------\n// This file contains all styles related to the button component.\n// -----------------------------------------------------------------------------\n\n/// Load custom module\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.button {\n  color: a.theme-color('dark');\n  background-color: a.theme-color('primary');\n  font-family: a.$font-stack-accent;\n  border-radius: a.$border-radius;\n  padding: 0.125rem 2rem;\n  line-height: 1;\n  cursor: pointer;\n  border: 0.125rem solid transparent;\n\n  &:focus-visible {\n    box-shadow: none;\n    outline: none;\n    border-color: a.theme-color('primary', 'light');\n    background-color: a.theme-color('primary', 'light');\n  }\n  &:hover:not(:disabled) {\n    background-color: a.theme-color('primary', 'dark');\n    border-color: a.theme-color('primary', 'dark');\n  }\n  &:focus {\n    border-color: a.theme-color('background');\n    outline: 0.0675rem solid a.theme-color('primary');\n  }\n  &:disabled {\n    background-color: a.theme-color('primary', 'transparent');\n    color: a.theme-color('dark', 'transparent');\n    cursor: default;\n  }\n  &:active:not(:disabled) {\n    background-color: a.theme-color('primary', 'light');\n    border-color: a.theme-color('primary', 'light');\n    outline: none;\n  }\n}\n","// -----------------------------------------------------------------------------\n// This file contains styles that are specific to the contact us page.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.content__contact-us {\n  display: flex;\n  flex-direction: column;\n\n  .contact-us__title {\n    text-align: center;\n    padding-bottom: 3rem;\n    @include a.text-setting(800);\n    font-weight: 600;\n  }\n\n  .contact-us__container {\n    @include a.text-setting(500);\n    display: flex;\n    justify-content: center;\n    gap: 5rem;\n\n    .contact-us__contact {\n      .contact__address,\n      .contact__phone,\n      .contact__email {\n        padding-bottom: 1rem;\n      }\n\n      .address__title,\n      .phone__title,\n      .email__title {\n        font-weight: 600;\n        line-height: 1.5rem;\n      }\n\n      .address__text,\n      .phone__text,\n      .email__text {\n        color: a.theme-color('primary');\n        line-height: 1;\n      }\n    }\n\n    .contact-us__order {\n      .order__form {\n        display: grid;\n        grid-template-rows: 1fr 1fr 2fr;\n        align-items: top;\n        gap: 1rem;\n\n        input,\n        textarea {\n          color: a.theme-color('light');\n          line-height: 1;\n          padding: 0.75rem;\n          height: 100%;\n          width: 100%;\n          background: none;\n          border: 0.075rem solid a.theme-color('primary');\n          border-radius: a.$border-radius;\n        }\n\n        *::placeholder {\n          color: a.theme-color('light', 'transparent');\n        }\n\n        .form__personal-details {\n          display: grid;\n          grid-template-columns: repeat(2, 1fr);\n          gap: 1rem;\n        }\n        .form__button-container {\n          .form__button {\n            padding: 0.625rem 2rem;\n          }\n        }\n      }\n    }\n  }\n}\n","// -----------------------------------------------------------------------------\n// This file contains styles that are specific to the home page.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.content__home {\n  margin: 0 auto;\n  max-width: 80dvw;\n  max-height: 60dvh;\n  display: grid;\n  grid-template-columns: repeat(6, 1fr);\n  grid-template-rows: 100%;\n  align-items: center;\n\n  .home__image {\n    background-color: a.theme-color('foreground');\n    border-radius: a.$border-radius;\n    grid-column: 3 / -1;\n    grid-row: 1 / 2;\n    height: 100%;\n    justify-self: right;\n  }\n\n  .home__order {\n    justify-self: left;\n    background-color: a.theme-color('background', 'transparent');\n    grid-column: 1 / 4;\n    grid-row: 1 / 2;\n    border: 0.075rem solid a.theme-color('primary');\n    border-radius: a.$border-radius;\n    padding: 3rem 4rem;\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n\n    &__text {\n      @include a.text-setting(800);\n      line-height: 1;\n    }\n\n    &__button {\n      align-self: flex-end;\n      @include a.text-setting(700);\n      font-weight: 600;\n      margin-top: 3rem;\n      border-radius: 1rem;\n    }\n  }\n}\n","// -----------------------------------------------------------------------------\n// This file contains styles that are specific to the menu page.\n// -----------------------------------------------------------------------------\n\n/// Load custom modules\n@use '../abstracts' as a;\n@use '../themes' as t;\n\n.content__menu {\n  .menu__item {\n    margin: 2rem auto;\n    display: flex;\n    max-width: min(900px, 80dvw);\n    align-items: center;\n\n    .item__desc {\n      padding: 2rem;\n      &__title {\n        color: a.theme-color('primary');\n        @include a.text-setting(700);\n        text-align: right;\n        border-bottom: 0.075rem solid a.theme-color('primary');\n        line-height: 1;\n        padding-bottom: 0.625rem;\n        margin-bottom: 0.625rem;\n      }\n      &__text {\n        text-align: right;\n      }\n    }\n\n    .item__image {\n      border-radius: a.$border-radius;\n      max-width: 50%;\n    }\n  }\n\n  .menu__item:nth-child(2n) {\n    flex-direction: row-reverse;\n    & > * > {\n      & p,\n      & h2 {\n        text-align: left;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
